import request from '@/utils/request'
import axios from 'axios'
// 查询當前用户信息
export function getInfo() {
  return request({
    url: '/sysUsers/getInfo',
    method: 'get'
  })
}

// 分页查询用户信息
export function pageList(query) {
  return request({
    url: '/sysUsers/pageList',
    method: 'get',
    params: query,
  })
}

// 不分页查询用户信息
export function list(data) {
  return request({
    url: '/sysUsers/list',
    method: 'post',
    data: data
  })
}

// 添加用户信息
export function insert(data) {
  return request({
    url: '/sysUsers/insert',
    method: 'post',
    data: data
  })
}

// 修改用户信息
export function update(data) {
  return request({
    url: '/sysUsers/update',
    method: 'put',
    data: data,
  })
}

// 添加或修改用户信息
export function save(data) {
  return request({
    url: '/sysUsers/save',
    method: 'post',
    data: data
  })
}

// 删除用户信息
export function del(id) {
  return request({
    url: '/sysUsers/'+id,
    method: 'delete'
  })
}

// 根据id查询用户信息
export function onebyid(id) {
  return request({
    url: '/sysUsers/'+id,
    method: 'get'
  })
}