<template>
  <div>
    <a-form layout="inline" :form="query" style="margin-bottom: 14px;">
      <a-form-item label="用戶名">
        <a-input v-model="query.username" />
      </a-form-item>
      <a-form-item label="姓名">
        <a-input v-model="query.name" />
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
        <a-button type="primary" html-type="submit" @click="findSubmit()" icon="search">查找</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
        <a-button html-type="submit" @click="add()" icon="plus">添加</a-button>
      </a-form-item>
    </a-form>
    <a-table :columns="columns" size="small" :data-source="data" rowKey="id">
      <span slot="action" slot-scope="record">
        <a @click="edit(record)"><a-icon type="edit" /></a>
        <a-divider type="vertical" />
        <a-popconfirm
          title="您確定要刪除該數據嗎?"
          ok-text="Yes"
          cancel-text="No"
          @confirm="deluser(record.id)"
        >
        <a style="color:red"><a-icon type="delete" /></a>
        </a-popconfirm>
      </span>
      <span slot="tags" slot-scope="tags">
        <!-- <a-tag>
          {{ tags}}
        </a-tag> -->
        <a-tag
          v-for="tag in tags.split(',')"
          :key="tag"
          :color="tag === 'Mongoose' ? 'geekblue' : (tag === 'Bison' ? 'volcano' : 'green')"
        >
          {{ tag}}
        </a-tag>
      </span>
      <a-descriptions
        slot="expandedRowRender"
        slot-scope="record"
        style="margin: 0"
      >
        <a-descriptions-item label="用戶名">
          {{ record.username }}
        </a-descriptions-item>
        <a-descriptions-item label="姓名">
          {{ record.name }}
        </a-descriptions-item>
        <a-descriptions-item label="密碼">
          {{ record.password }}
        </a-descriptions-item>
        <a-descriptions-item label="級別">
          {{ record.power }}
        </a-descriptions-item>
        <a-descriptions-item label="機種">
          {{ record.model }}
        </a-descriptions-item>
        <a-descriptions-item label="階段">
          {{ record.build }}
        </a-descriptions-item>
        <a-descriptions-item label="Mail發送名單">
          {{ record.process }}
        </a-descriptions-item>
        <a-descriptions-item label="登錄時間">
          {{ record.logintime }}
        </a-descriptions-item>
        <a-descriptions-item label="更改密碼時間">
          {{ record.pwdtime }}
        </a-descriptions-item>
        <a-descriptions-item label="密碼更改人">
          {{ record.pwdbyuser }}
        </a-descriptions-item>
        <a-descriptions-item label="權限更改時間">
          {{ record.powertime }}
        </a-descriptions-item>
        <a-descriptions-item label="權限更改人">
          {{ record.powerbyuser }}
        </a-descriptions-item>
      </a-descriptions>
    </a-table>

    <!-- 添加或修改model -->
    <a-modal
      :title="title"
      :visible="visible"
      :zIndex="9"
      @ok="handleOk"
      @cancel="handleCancel"
      cancelText="关闭"
      okText="確定"
    >
      <a-form-model
        ref="ruleForm"
        :rules="rules"
        :model="formData"
        :label-col="{ span: 6 }"
        :wrapper-col="{ span: 16 }"
      >
        <a-form-model-item label="id"  prop="id" style="display:none">
          <a-input v-model="formData.id" />
        </a-form-model-item>
        <a-form-model-item label="用戶名" prop="username">
          <a-input v-model="formData.username" />
        </a-form-model-item>
        <a-form-model-item label="姓名" prop="name">
          <a-input v-model="formData.name" />
        </a-form-model-item>
        <a-form-model-item label="權限" prop="power">
          <a-select v-model="formData.power" placeholder="請選擇權限" :disabled="this.formData.power=='admin'||this.formData.power=='IE_1'">
            <a-select-option value="admin">
              admin
            </a-select-option>
            <a-select-option value="IE_1">
              IE_1
            </a-select-option>
            <a-select-option value="IE_2">
              IE_2
            </a-select-option>
            <a-select-option value="ME_1">
              ME_1
            </a-select-option>
            <a-select-option value="ME_2">
              ME_2
            </a-select-option>
            <a-select-option value="AE_1">
              AE_1
            </a-select-option>
            <a-select-option value="AE_2">
              AE_2
            </a-select-option>
            <a-select-option value="FE_1">
              FE_1
            </a-select-option>
            <a-select-option value="FE_2">
              FE_2
            </a-select-option>
            <a-select-option value="PE_1">
              PE_1
            </a-select-option>
            <a-select-option value="PE_2">
              PE_2
            </a-select-option>         
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="密碼" prop="password">
          <a-input v-model="formData.password" />
        </a-form-model-item>
        <a-form-model-item label="幾種" prop="qmodel">
          <a-checkbox-group v-model="formData.qmodel">
            <a-checkbox value="Mongoose" name="qmodel">
              Mongoose
            </a-checkbox>
            <a-checkbox value="Bison" name="qmodel">
              Bison
            </a-checkbox>
            <a-checkbox value="Badger" name="qmodel">
              Badger
            </a-checkbox>
          </a-checkbox-group>
        </a-form-model-item>
        <a-form-model-item label="階段" prop="build">
          <a-input v-model="formData.build" />
        </a-form-model-item>
        <a-form-model-item label="Mail發送名單" prop="process">
          <a-input v-model="formData.process" />
        </a-form-model-item>
              </a-form-model>

    </a-modal>
  </div>
</template>
<script>
import security from '@/utils/security'
const columns = [
  { title: "用户名", dataIndex: "username" },
  { title: "姓名", dataIndex: "name" },
  { title: "密码", dataIndex: "password" },
  { title: "級別", dataIndex: "power" },
  { title: "機種", dataIndex: "model" ,
    scopedSlots: { customRender: 'tags' },
    width: 250,
  },
  { title: "階段", dataIndex: "build" },
  { title: "Mail發送名單", dataIndex: "process",ellipsis: true},
  {
    title: "操作",
    dataIndex: "",
    key: "x",
    scopedSlots: { customRender: "action" },
  },
];

import { pageList, getInfo, save,del } from "@/api/system/user";
export default {
  data() {
    return {
      data: [],
      columns,
      query: {
        limit: 10,
        page: 1,
        username: "",
        name: "",
        power: "",
      },
      role:security.getUser(),
      title: "用戶修改",
      visible: false,
      delvisible: false,
      formData: {
        username: "",
        password: "",
        name: "",
        power: "",
        model: "",
        qmodel: [],
        build: "",
        process: "",
        pwdtime: "",
        pwdbyuser: "",
        powertime: "",
        powerbyuser: ""
      },
      rules: {
        id: [
          { required: false },
        ],
        username: [
          { required: true, message: "請輸入用戶名", trigger: "change" },
        ],
        name: [{ required: true, message: "請輸入密碼", trigger: "change" }],
        password: [
          { required: true, message: "請輸入密碼", trigger: "change" },
        ],
        power: [{ required: true, message: "請選擇權限", trigger: "change" }],
        qmodel: [{ required: true, message: "請選擇幾種", trigger: "change" }],
        process: [
          { required: true, message: "請選擇郵件發送名單", trigger: "change" },
        ],
      },
    };
  },
  methods: {
    findSubmit() {
      pageList(this.query).then((res) => {
        this.data = res.data;
      });
    },
    edit(record) {
      this.title="用戶修改"
      this.visible = true;
      setTimeout(()=>{
      this.$refs.ruleForm && (this.$refs.ruleForm.resetFields() || this.$refs.ruleForm.clearValidate())
      Object.assign(this.formData,record,{qmodel:record.model.split(",")})
      })

    },
    deluser(id) {
      del(id).then((res) => {
        if (res.code == 200) {
          this.$message.success("刪除成功");
          this.findSubmit()
        }
      });
    },
    add(){
      this.title="用戶添加"
      this.$refs.ruleForm && (this.$refs.ruleForm.resetFields() || this.$refs.ruleForm.clearValidate())
      this.visible = true
    },
    //添加或修改提交
    handleOk() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          if(this.role=='IE_1'&&(this.formData.power=='admin'||this.formData.power=='IE_1')){
            this.$message.error("不可以設置比自身大的權限");
          }
          else{
            //獲取編輯用戶的信息
            getInfo().then(res => {
              this.formData.powerbyuser=res.data.username;
              this.formData.pwdbyuser=res.data.username;
              this.formData.powertime=new Date();
              this.formData.pwdtime=new Date();
              this.formData.model=this.formData.qmodel.join(",");
              //發送請求
              save(this.formData).then((res) => {
                if (res.code == 200&&this.formData.id===undefined) {
                  this.$message.success("添加成功");
                }
                else{
                  this.$message.success("修改成功");
                }
                this.visible = false;
                this.findSubmit()
              });
            })
          }
        }
        else{
           return false;
        }
      })
    },
    handleCancel() {
      this.visible = false;
      this.$refs.ruleForm && (this.$refs.ruleForm.resetFields() || this.$refs.ruleForm.clearValidate())
      this.findSubmit()
    },
  },
  created: function () {
    pageList(this.query).then((res) => {
      this.data = res.data;
    });
  },
};
</script>